<template>
  <div>
    <!-- 이미지 -->
    <svg
      class="bd-placeholder-img card-img-top"
      width="300px"
      height="200"
      xmlns="http://www.w3.org/2000/svg"
      role="img"
      aria-label="Placeholder: Image cap"
      preserveAspectRatio="xMidYMid slice"
      focusable="false"
    >
      <title>Placeholder</title>
      <rect width="100%" height="100%" fill="#868e96" />
      <text x="50%" y="50%" fill="#dee2e6" dy=".3em">Image cap</text>
    </svg>

    <!-- 메뉴 상세 내용 -->
    <p>메뉴 상세 (설명) 내용 {{ menu.finfo }}</p>

    <!-- 가격 옵션 -->
    <div class="list-group">
      <label class="list-group-item d-flex gap-2">
        <input
          class="form-check-input flex-shrink-0"
          type="radio"
          name="listGroupRadios"
          id="listGroupRadios1"
          value=""
          checked
        />
        <span>
          기본
          <small class="d-block text-body-secondary">+0원</small>
        </span>
      </label>
      <label class="list-group-item d-flex gap-2">
        <input
          class="form-check-input flex-shrink-0"
          type="radio"
          name="listGroupRadios"
          id="listGroupRadios2"
          value=""
        />
        <span>
          곱빼기
          <small class="d-block text-body-secondary">+500원</small>
        </span>
      </label>
    </div>

    <!-- 수량 조절 -->
    <div class="MS_sp">
      <p>
        수량:
        <b-form-spinbutton id="sb-input" v-model="cnt" wrap></b-form-spinbutton>
      </p>
    </div>

    <!-- 총 금액 -->
    <div class="input-group mb-3" style="width: 250px">
      <button
        class="btn btn-outline-secondary"
        type="button"
        @click="increment"
      >
        +
      </button>
      <input
        type="number"
        class="form-control"
        placeholder=""
        aria-label="Example text with two button addons"
        v-model="cnt"
      />
      <button
        class="btn btn-outline-secondary"
        type="button"
        @click="decrement"
      >
        -
      </button>
      <p>총금액: {{ calculateTotal() }}원</p> <!-- 총 금액 계산 -->
    </div>

    <!-- 담기 버튼 -->
    <button type="button" @click="addToCart">담기</button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "C_MenuInf",
  data() {
    return {
      selected: "",
      cnt: 1,
      menu: {},
    };
  },
  methods: {
    increment() {
      this.cnt++;
    },
    decrement() {
      if (this.cnt > 1) {
        this.cnt--;
      }
    },
    async fetchData() {
      try {
        const response = await axios.get("http://localhost:8888/C_MRcardInf");
        this.menu = response.data;
      } catch (error) {
        console.error("Error fetching menu data:", error);
      }
    },
    calculateTotal() {
      // 총 금액 계산 로직 추가
      return this.menu.fprice * this.cnt;
    },
    addToCart() {
      // 장바구니에 담기 로직 추가
      // 담긴 메뉴 정보와 수량을 전달하거나, 상태 관리를 통해 처리
    },
  },
};
</script>

<style>
.MS_Price {
  text-align: left;
}
.MS_PChoose {
  text-align: center;
}
.MS_sp {
  text-align: left;
}
#sb-input {
  width: 150px;
  margin-top: -25px;
  position: relative;
  left: 350px;
}
</style>
