import {createRouter, createWebHistory} from 'vue-router';
//import HomeView from '../views/client/C_Menu.vue';
import C_ShopInf from '@/views/client/C_ShopInf.vue';
import C_MenuInf from '@/views/client/C_MenuInf.vue';
import store from '@/store/JH_Auth_Store';

const routes = [
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import (/* webpackChunkName: "about" */ '../views/AboutView.vue'),
  },
  {
    path: '/:level/login',
    name: 'login',
    component: () => import ('@/views/comm/JH_Login.vue'),
  },
  {
    path: '/:level/findMyEmail',
    name: 'findMyEmail',
    component: () => import ('@/views/comm/JH_FindMyEmail.vue'),
  },
  {
    path: '/:level/findMyPw',
    name: 'findMyPw',
    component: () => import ('@/views/comm/JH_FindMyPw.vue'),
  },

  {
    path: '/selectMemberType',
    name: 'selectMemberType',
    component: () => import ('@/views/comm/JH_SelectMemberType.vue'),
  },

  {
    path: '/Info',
    name: 'Info',
    component: () => import ('@/views/client/C_MenuInf.vue'),
  },
  {
    path: '/B_ShopTimeUpdate',
    name: 'ShopRevise',
    component: () => import ('@/views/business/MS_B_ShopRevise.vue'),
  },
  {
    path: '/C_MainCategory',
    name: 'MainCategory',
    component: () => import ('@/views/client/C_MainCategory.vue'),
  },
  {
    path: '/Kfood',
    component: () => import ('@/views/client/C_KoreanFood.vue'),
  },
  {
    path: '/Wfood',
    component: () => import ('@/views/client/C_WesternFood.vue'),
  },
  {
    path: '/Jfood',
    component: () => import ('@/views/client/C_JapaneseFood.vue'),
  },
  {
    path: '/Cfood',
    component: () => import ('@/views/client/C_ChineseFood.vue'),
  },
  {
    path: '/KSfood',
    component: () => import ('@/views/client/C_KoreansStreetFood.vue'),
  },
  {
    path: '/menu/:id', // 동적 경로 매개변수를 사용하여 각 메뉴의 ID를 전달합니다.
    name: 'C_ShopInf',
    component: C_ShopInf,
    props: true,
  },
  {
    path: '/menu',
    name: 'MenuInf',
    component: C_MenuInf,
  },
  {
    path: '/C_MenuInf',
    name: 'MenuInf',
    component: () => import ('@/views/client/C_MenuInf.vue'),
  },
  {
    path: '/business/B_Ordering',
    name: 'B_Ordering',
    component: () => import ('@/views/business/MS_B_Ordering.vue'),
  },

  {
    path: '/business/join',
    name: 'B_Join',
    component: () => import ('@/views/business/MS_B_Join.vue'),
  },
  {
    path: '/business/Files',
    name: 'B_Files',
    component: () => import ('@/views/business/MS_B_Files.vue'),
  },
];

const router = createRouter ({
  history: createWebHistory (process.env.BASE_URL),
  routes,
});

router.beforeEach ((to, from, next) => {
  const isLoggedIn = store.getters.isLoggedIn;
  if (to.path === '/login' && isLoggedIn) {
    next ('/home');
  } else {
    next ();
  }
});

export default router;
