<template>
  <div class="container">
    <div class="MSMenu" data-masonry='{"percentPosition": true }'>
      <router-link to="/menu">
        <div
          v-for="menu in menuInfo"
          :key="menu.fid"
          class="card_box col-lg-4 mb-4"
        >
          <div class="card h-100">
            <!-- 이미지 -->
            <div class="StoreImg" style="margin-top: 10px">
              <img
                :src="menu.fimg"
                class="card-img-top"
                alt="Restaurant Image"
              />
            </div>
            <!-- 내용 -->
            <div class="MRcard-body" style="margin-top: 20px">
              <h5 class="MRcard_Name">메뉴명: {{ menu.fname }}</h5>
              <p class="MRcard_Price">가격:{{ menu.fprice }}</p>
              <p class="card-text"></p>
            </div>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "C_MRcard",
  data() {
    return {
      // snames: [], // 상점 목록을 저장할 배열
      menuInfo: [], // 메뉴 정보를 저장할 객체
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      await axios
        .get("/C_MRcardInf") // 백엔드에서 메뉴 데이터를 제공하는 엔드포인트로 수정
        .then((response) => {
          this.menuInfo = response.data; // 응답 데이터를 'menus' 배열에 저장
        })
        .catch((error) => {
          console.error("Error fetching menu data:", error);
        });
    },
    async MenuInfdetails(fnum) {
      try {
        // 라우터를 사용하여 페이지 이동
        this.$router.push({ path: "/C_MenuInf", query: { fnum } });
      } catch (error) {
        console.error("Error fetching noticeDetails:", error);
      }
    },
  },
};
</script>

<style scoped>
/* 카드 컨테이너 수정 */
.card_box {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin: 0 auto; /* 가운데 정렬을 위한 수정 */
}

/* 카드 이미지 */
.card-img-top {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

/* 메뉴명 */
.MRcard_Name {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

/* 메뉴 가격 */
.MRcard_Price {
  font-size: 1.2rem;
  color: #6c757d;
  margin-bottom: 1rem;
}

.card:hover {
  transform: translateY(-5px);
}

/* 미디어 쿼리: 화면 크기가 작을 때 */
@media screen {
  /* 카드 이미지 */
  .card-img-top {
    height: 150px;
  }
}
</style>
